import React, { Component } from "react";
import { dataCommentTest } from "../Utils/dataCommentTest";
class showCommentTest extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let dataSave = JSON.parse(sessionStorage.dataSave);
    let dataChuong = JSON.parse(sessionStorage.dataChuong);
    let contentNxTest = [];
    let dataCmt = dataCommentTest(dataSave, dataChuong);
    for (const x in dataCmt) {
      if (dataCmt.hasOwnProperty(x)) {
        const el = dataCmt[x];
        contentNxTest.push(el);
      }
    }
    let contentNxTests = contentNxTest.map(
      (nx, index) => {
        console.log(nx);
        let nhanxet = "";
        switch (nx.evaluate) {
          case "Yu":
            nhanxet =
              "Kin thc ca bn  ni dung ny cn rt hn ch. Bn cn hc l thuyt v lm bi tp nhiu hn.";
            break;
          case "Trung bnh":
            nhanxet =
              "Kin thc  ni dung ny ca bn ch tm chp nhn. Tuy nhin bn cn phi nng cao thm  t kt qu tt hn";
            break;
          case "Kh":
            nhanxet =
              "Kin thc ca bn  ni dung ny kh tt. Bn cn n thm nhiu dng bi tp mi  phn ny cng hon ho hn";
            break;
          case "Tt":
            nhanxet =
              "Kin thc ca bn  phn ny rt tt. Mc d vy bn khng nn chnh mng m tip tc gi vng phong  nh ny nh";
            break;
        }
        return (
          <tr key={index}>
            <td className="h6 font-weight-normal">{nx.nameContent}</td>
            <td
              className="h6 text-center font-weight-normal"
              style={{ color: "#dc3545" }}
            >
              {nx.evaluate}
            </td>
            <td className="h6 font-weight-normal">
              <div>
                - Bn  lm ng {nx.correctAnswer}/{nx.sumCorrect} (t t l{" "}
                {parseFloat(nx.correctAnswer / nx.sumCorrect).toFixed(2) * 100}{" "}
                % )
              </div>
              <div>- {nhanxet}</div>
            </td>
          </tr>
        );
      }
      //<li key={index}>Ni dung {nx.nameContent}:&nbsp;&nbsp;<i style={{color:"#dc3545"}}>{nx.evaluate}</i></li>
    );
    return (
      <div style={{ fontWeight: "500" }} className="">
        <div className="font-option mb-3">Nhn xt nh gi cc ni dung</div>
        <div style={{ width: 800 + "px" }}>
          <tr>
            <th className="text-center">Tn ni dung</th>
            <th className="text-center" style={{ width: 200 + "px" }}>
              nh gi
            </th>
            <th className="text-center">Nhn xt</th>
          </tr>

          {contentNxTests}
        </div>
      </div>
    );
  }
}
export default showCommentTest;
